import combinate from "combinate";
import { useContext, useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import swal from "sweetalert";

import useAsync from "./useAsync";
import { SidebarContext } from "context/SidebarContext";
import { notifyError, notifySuccess } from "utils/toast";
import SettingServices from "services/SettingServices";
import { AddStaff, editStaff } from "services/Api";
import { useHistory, useLocation } from "react-router-dom";

const useProductSubmit = (id, getAllStayList, SingleStay) => {
  const { isDrawerOpen, closeDrawer, setIsUpdate, lang } =
    useContext(SidebarContext);

  const { data: globalSetting } = useAsync(SettingServices.getGlobalSetting);
  const resetRefTwo = useRef("");
  const [imageUrl, setImageUrl] = useState([]);
  const [imageUrl1, setImageUrl1] = useState([]);
  const [tag, setTag] = useState([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [values, setValues] = useState({});
  let [variants, setVariants] = useState([]);
  const [variant, setVariant] = useState([]);
  const [tapValue, setTapValue] = useState("Basic Info");
  const [isCombination, setIsCombination] = useState(false);
  const [SatyList, setSatyList] = useState([]);
  const [imgId, setImgId] = useState("");
  const [dates, setDates] = useState([]);
  const [amminitiesList, setamminitiesList] = useState([]);
  const [isBulkUpdate, setIsBulkUpdate] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState([]);
  const [defaultCategory, setDefaultCategory] = useState([]);
  const [defaultCategory1, setDefaultCategory1] = useState(null);
  const [resaddess, setresaddess] = useState({});
  const [language, setLanguage] = useState(lang);
  const history = useHistory();
  const [openModal, setOpenModal] = useState(false);
  const [slug, setSlug] = useState("");
  const [propertyList, setpropertyList] = useState([]);
  const onCloseModal = () => setOpenModal(false);
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    formState: { errors },
  } = useForm();
  const resetBtn = () => {
    console.log("fsefh");
    history.replace("/staff");
    reset();
  };
  const onSubmit = async (data) => {
    setIsSubmitting(true);
    const propertyImages = imageUrl;
    // if (imageUrl.length === 0 || imageUrl1.length === 0) {
    //   setTimeout(() => {
    //     setIsSubmitting(false)
    //     }, 2000);
    //   return notifyError("File is empty. Please select a valid file.");
    // }
    console.log("Day out package----", data, dates, imageUrl1, imageUrl);
    try {
      let payload = {
        property_id: data?.PropertyID,
        staff_schedule_from_time: data?.Staffjoiningfromtime,
        staff_schedule_to_time: data?.Staffjoiningtotime,
        blood_group: data?.BloodGroup,
        first_name: data?.FirstName,
        last_name: data?.Lastname,
        mobile: data?.Phone,
        email: data?.Email,
        profile_pic: propertyImages[0]?.image,

        id_proof_type: data?.typeIdProof,
        id_doc_url: imageUrl1[0]?.image,
        marital_status: data?.Maritalstatus,
        languages_known: data?.Language,
        postal_code: data?.postalcode,
        bank_name: data?.BankName,
        bank_account_number: data?.AccountNumber,
        bank_ifsc_code: data?.Ifsc,
        bank_branch_name: data?.BranchName,
        emergency_contact_name: data?.custoName,
        emergency_contact_relation: data?.ContactRelation,
        emergency_contact_number: data?.custoPhone,
        designation: data?.Designation,
        Staff_schedule_days: data?.Staffsecduledday,
      };
      if (data?.Password) {
        payload["password"] = data?.Password;
      }
      let res = {};
      if (data?.id) {
        payload["id"] = data?.id;
        res = await editStaff(payload);
      } else res = await AddStaff(payload);
      if (res.data.status == true) {
        console.log(res, "dsfs");
        closeDrawer();
        setIsSubmitting(false);
        handleClearVariant();
        notifySuccess(res.data.message);
        history.replace("/staff");
        // getAllStayList(1);
      }
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
  };

  const handleClearVariant = () => {
    setImageUrl([]);
    setImageUrl1([]);
    setValue("PropertyId", "");
    setValue("name", "");
    setValue("Tent", "");
    setValue("Number_of_sharing", "");
    setValue("Max_guest_capacity", "");
    setValue("propertyImages", "");
    setValue("Aminities", "");
  };

  //for edit combination values
  const handleEditVariant = (variant) => {
    setTapValue("Combine");
  };

  //for remove combination values
  const handleRemoveVariant = (vari, ext) => {
    // console.log("handleRemoveVariant", vari, ext);
    swal({
      title: `Are you sure to delete this ${ext ? "Extra" : "combination"}!`,
      text: `(If Okay, It will be delete this ${
        ext ? "Extra" : "combination"
      })`,
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        const result = variants.filter((v) => v !== vari);
        setVariants(result);
        // console.log("result", result);
        const {
          originalPrice,
          price,
          discount,
          quantity,
          barcode,
          sku,
          productId,
          image,
          ...rest
        } = vari;
        const res = variant.filter(
          (obj) => JSON.stringify(obj) !== JSON.stringify(rest)
        );
        setVariant(res);
        setIsBulkUpdate(true);
        // setTimeout(() => setIsBulkUpdate(false), 500);
        const timeOutId = setTimeout(() => setIsBulkUpdate(false), 500);
        return clearTimeout(timeOutId);
      }
    });
  };

  return {
    tag,
    setTag,
    values,
    language,
    register,
    onSubmit,
    errors,
    slug,
    setValue,
    openModal,
    setValues,
    variants,
    imageUrl,
    setImageUrl,
    imageUrl1,
    setImageUrl1,
    handleSubmit,
    isCombination,
    onCloseModal,
    isBulkUpdate,
    globalSetting,
    tapValue,
    setTapValue,
    selectedCategory,
    setSelectedCategory,
    setDefaultCategory,
    resaddess,
    setresaddess,
    defaultCategory,
    resetBtn,
    setIsSubmitting,
    isSubmitting,
    setDefaultCategory1,
    propertyList,
    setpropertyList,
    defaultCategory1,
    setSatyList,
    SatyList,
    setamminitiesList,
    amminitiesList,
    setDates,
    dates,
    handleEditVariant,
    handleRemoveVariant,
    handleClearVariant,
  };
};

export default useProductSubmit;
