import { Button, Select } from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import Error from "components/form/Error";
import spinnerLoadingImage from "assets/img/spinner.gif";
import InputAreaTwo from "components/form/InputAreaTwo";
import PageTitle from "components/Typography/pageTitleButton";
import useSettingSubmit from "hooks/useAddStaffSubmit";
import Uploader from "components/image-uploader/Uploader";
import Uploader1 from "components/image-uploader/docUploader";
import useProductSubmit from "hooks/useProductSubmit";
import { useEffect, useState } from "react";
import { getAllCommonProperyList } from "services/Api";
const Setting = () => {
  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    setDates,
    resetBtn,
    dates,
    isSave,
    isSubmitting,
    resetRefTwo,
    setIsSubmitting,
    setDefaultCategory,
    defaultCategory,
    imageUrl,
    setImageUrl,
    imageUrl1,
    setImageUrl1,
  } = useSettingSubmit();

  const { t } = useTranslation();
  const format = "MM/DD/YYYY";
  const LanguageList = [
    { name: "Kannada", id: "Kannada" },
    { name: "English", id: "English" },
    { name: "Hindi", id: "Hindi" },
    { name: "Tamil", id: "Tamil" },
  ];
  const [propertyList, setpropertyList] = useState([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    getPropertyList();
  }, []);
  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setpropertyList(PropertyList?.data?.data?.propertyList);
    setLoading(false);
  };

  const setDates1 = (e) => {
    console.log(e, "eee");
    setDates(e?.map((date) => date?.format("MM/DD/YYYY")));
  };

  return (
    <>
      <PageTitle resetBtn={resetBtn}>{t("Add Staff")}</PageTitle>

      <div className="container p-6 mx-auto bg-white  dark:bg-gray-800 dark:text-gray-200 rounded-lg">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="grid grid-cols-12 font-sans">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select property")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`PropertyID`, {
                          required: "Select property is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select property")}
                        </option>
                        {propertyList?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.PropertyID} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("First Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("First Name")}
                        name="FirstName"
                        type="text"
                        required
                        placeholder={t("Enter First Name")}
                      />
                      <Error errorName={errors.FirstName} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Last Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Last Name")}
                        name="Lastname"
                        type="text"
                        placeholder={t("Enter Last Name")}
                      />
                      <Error errorName={errors.LastName} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Password")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Password")}
                        name="Password"
                        type="text"
                        required
                        placeholder={t("Enter Password")}
                      />
                      <Error errorName={errors.Password} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Staff Wroking From time")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Staff Wroking From time")}
                        name="Staffjoiningfromtime"
                        type="time"
                        placeholder={t("Enter Staff working from time")}
                      />
                      <Error errorName={errors.Staffjoiningfromtime} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Staff Working to time")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Staff Working to time")}
                        name="Staffjoiningtotime"
                        type="time"
                        placeholder={t("Enter Staff Working to time")}
                      />
                      <Error errorName={errors.Staffjoiningtotime} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Designation")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`Designation`, {
                          required: "Select Designation is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Designation")}
                        </option>
                        <option value="housekeeper">Housekeeper</option>
                        <option value="waiter">Waiter</option>
                        <option value="receptionist">Receptionist</option>
                        <option value="accounts">Accounts</option>
                        <option value="manager">Manager</option>
                      </Select>
                      <Error errorName={errors.Designation} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Phone")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Phone")}
                        name="Phone"
                        type="text"
                        required
                        maxLength={10}
                        placeholder={t("Enter Phone")}
                      />
                      <Error errorName={errors.Phone} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Email")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Email")}
                        name="Email"
                        type="text"
                        required
                        placeholder={t("Enter Email")}
                      />
                      <Error errorName={errors.Email} pos />
                    </div>
                  </div>
                </div>

                <h1>
                  <b> Emergency contact details </b>
                </h1>
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Name")}
                        name="custoName"
                        type="text"
                        placeholder={t("Enter contact Name")}
                      />
                      <Error errorName={errors.custoName} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Phone")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Phone")}
                        name="custoPhone"
                        type="text"
                        placeholder={t("Enter contact Phone")}
                      />
                      <Error errorName={errors.custoPhone} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Contact Relation")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Relation")}
                        name="ContactRelation"
                        type="text"
                        placeholder={t("Enter ContactRelation")}
                      />
                      <Error errorName={errors.ContactRelation} pos />
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Blood Group")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Blood Group")}
                        name="BloodGroup"
                        type="text"
                        placeholder={t("Enter Blood Group")}
                      />
                      <Error errorName={errors.BloodGroup} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Marital status")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`Maritalstatus`, {
                          required: "Select Marital status is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Marital status")}
                        </option>
                        <option value="true">married</option>
                        <option value="false">Un married</option>
                      </Select>
                      <Error errorName={errors.Maritalstatus} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Mother Tongue")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`Language`, {
                          required: "Select Mother Tongue is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Mother Tongue")}
                        </option>
                        {LanguageList?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.Language} pos />
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-8 items-center sm:grid-cols-4 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-4"></div>
                <h1>
                  <b> Bank Details </b>
                </h1>
                <div className="grid md:grid-cols-8 items-center sm:grid-cols-4 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-4">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Bank Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Bank Name")}
                        name="BankName"
                        type="text"
                        placeholder={t("Enter Bank Name")}
                      />
                      <Error errorName={errors.BankName} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Ifsc")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Ifsc")}
                        name="Ifsc"
                        type="text"
                        placeholder={t("Enter Ifsc")}
                      />
                      <Error errorName={errors.Ifsc} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Branch Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Branch Name")}
                        name="BranchName"
                        type="text"
                        placeholder={t("Enter Branch Name")}
                      />
                      <Error errorName={errors.BranchName} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Account Number")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Account Number")}
                        name="AccountNumber"
                        type="text"
                        placeholder={t("Enter Account Number")}
                      />
                      <Error errorName={errors.AccountNumber} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select ID's")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`typeIdProof`, {
                          // required: "Select ID's is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select ID's")}
                        </option>
                        <option value="AdharCard">Adhar Card</option>
                        <option value="PanCard">PanCard</option>
                        <option value="Dl">DL</option>
                      </Select>
                      <Error errorName={errors.typeIdProof} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Postal code")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Postal Code")}
                        name="postalcode"
                        type="text"
                        placeholder={t("Enter postal code")}
                      />
                      <Error errorName={errors.postalcode} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Staff schedule day")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`Staffsecduledday`, {
                          required: "Staff schedule Day is required",
                        })}
                        multiple
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Staff schedule day")}
                        </option>
                        <option value="monday">Monday</option>
                        <option value="tuesday">Tuesday</option>
                        <option value="wednesday">Wednesday</option>
                        <option value="thursday">Thursday</option>
                        <option value="friday">Friday</option>
                        <option value="saturday">Saturday</option>
                        <option value="sunday">Sunday</option>
                      </Select>
                      <Error errorName={errors.Staffsecduledday} pos />
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-2 items-center sm:grid-cols-4 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-4">
                  <div className="">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Staff Photo")}
                    </label>
                    <Uploader
                      product
                      folder="product"
                      multiple={false}
                      imageUrl={imageUrl}
                      setImageUrl={setImageUrl}
                    />

                    <Error errorName={errors.totalAmount} pos />
                  </div>

                  <div className="">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("ID Proof")}
                    </label>
                    <Uploader1
                      product
                      folder="product"
                      imageUrl1={imageUrl1}
                      setImageUrl1={setImageUrl1}
                    />

                    <Error errorName={errors.totalAmount} pos />
                  </div>
                </div>
                <div className="flex flex-row-reverse pb-6">
                  <Button
                    type="button"
                    className="h-12 px-8"
                    onClick={() => resetBtn()}
                    style={{
                      position: "relative",
                      right: "22em",
                      backgroundColor: "gainsboro",
                      color: "red",
                    }}
                  >
                    Cancel
                  </Button>
                  {isSubmitting ? (
                    <Button disabled={true} type="button" className="h-12">
                      <img
                        src={spinnerLoadingImage}
                        alt="Loading"
                        width={20}
                        height={10}
                      />{" "}
                      <span className="font-serif ml-2 font-light">
                        Processing
                      </span>
                    </Button>
                  ) : (
                    <Button type="submit" className="h-12 px-8">
                      {" "}
                      {isSave ? "Save" : "Add Staff"}
                    </Button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};
export default Setting;
